.login-dark {
  background-color: #0e1318;
  height: 100vh;
  background-size: cover;
  position: relative;

  form {
    max-width: 320px;
    width: 90%;
    background-color: #1e2833;
    padding: 40px;
    border-radius: 4px;
    transform: translate(-50%, -50%);
    position: absolute;
    top: 50%;
    left: 50%;
    color: #fff;
    box-shadow: 3px 3px 4px rgba(0, 0, 0, 0.2);

    .form-control {
      background: none;
      border: none;
      border-bottom: 1px solid #434a52;
      border-radius: 0;
      box-shadow: none;
      outline: none;
      color: inherit;
    }

    .btn-primary {
      background: #009b84;
      border: none;
      border-radius: 4px;
      padding: 11px;
      box-shadow: none;
      margin-top: 26px;
      text-shadow: none;
      outline: none;

      &:hover,
      &:active {
        background: #214a80;
        outline: none;
        transform: translateY(1px);
      }
    }

    .forgot {
      display: block;
      text-align: center;
      font-size: 12px;
      color: #6f7a85;
      opacity: 0.9;
      text-decoration: none;
      &:hover,
      &:active {
        opacity: 1;
        text-decoration: none;
      }
    }
  }
}
